import React from 'react';
import FontAwesome from 'react-fontawesome';
import Social from '../social/social.js';

const SideBar = () => (
    <header id="header">
      <div className="inner">
        <Profile />
        <Social />
        <Contact />
      </div>
    </header>
  );



const Profile = () => (
    <div>
      <a className="image avatar">
        <img
          alt="Harsh Vardhan"
          src={require("../../images/Avatar.jpg")} />
      </a>
      <h1>
        <strong>Harsh Vardhan</strong>
      </h1>
      <h4>React Consultant</h4>
    </div>
  );
  
  const Contact = () => (
    <ul className="labeled-icons">
      <li>
        <h3 className="icon">
          <FontAwesome
            name='home'
            size='lg'
          />
          <span className="label">Address</span></h3>
        <h4 styles="text-align:left">Waterford<br /> Ireland</h4>
      </li>
      <li>
        <h3 className="icon">
          <FontAwesome
            name='mobile'
            size='lg'
          />
          <span className="label">Phone</span></h3>
        <h4 styles="text-align:left">083-045-1910</h4>
      </li>
      <li>
        <h3 className="icon">
          <FontAwesome
            name='envelope'
            size='lg'
          />
          <span className="label">Email</span></h3>
        <h4 styles="text-align:left">harsh.vardhan.611@gmail.com</h4>
      </li>
    </ul>
  );

  export default SideBar;


